import Vue from 'vue'
import Router from 'vue-router'
import BgMagic from '@/components/BgMagic'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/bgmagic',
      name: 'BgMagic',
      component: BgMagic
    },
    {
      path: '*',
      redirect: 'BgMagic'
    }
  ]
})



