<template>
    <div class="container">
        <form action="" v-on:submit.prevent="onSubmit">
            <div class="inputContainer">
                <span class="prefix">#</span>
                <input type="text" id="colorInput" placeholder="Enter color code" maxlength="6" v-model="color">
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios'
import io from 'socket.io-client'

let socket = io('/currentBG')

export default {
    data:() => ({
        color: ""
    }),
    mounted(){
        axios.get('/currentBG')
            .then(res => {
                document.body.style.backgroundColor = '#' + res.data['background-color']
        })

        socket.on('change', function(data){
            console.log("Color changed to #" + data.color)
            document.body.style.backgroundColor = '#' + data.color
        })
    },
    updated(){
       
    },
    methods: {
        onSubmit() {
            socket.emit('change', { color: this.color })
        },
        onColorChanged(data) {
            
        }
    }
}
</script>

<style scoped>
    @import url('https://fonts.googleapis.com/css?family=Varela+Round');

    div.container {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #colorInput {
        width: 100%;
        height: 60px;
        border-style: none;
        border-radius: 3px;
        font-size: 24px;
        box-sizing: border-box;
        -webkit-box-shadow: 0px 0px 33px -5px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 33px -5px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 33px -5px rgba(0,0,0,0.75);
        font-family: 'Varela Round', sans-serif;
        color: #444;
        padding-left: 42px;
    }

    div.inputContainer {
        max-width: 400px;
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
    }
    span.prefix {
        position: absolute;
        color: #CCC;
        vertical-align: center;
        font-family: 'Varela Round', sans-serif;
        font-size: 24px;
        margin-left: 18px;
    }

    #colorInput::placeholder {
        color: #CCC;
    }
</style>




